body {
  --header: 60px;
  --container: 50px;
  --sectionMargin: 120px;
  --containerFull: calc((100vw - 1340px) / 2);
  leading-trim: both;
  text-edge: cap;
}
@media (max-width: 1440px) {
  body {
    --containerFull: 50px;
  }
}
@media (max-width: 1024px) {
  body {
    --header: 62px;
    --container: 16px;
    --sectionMargin: 70px;
    --containerFull: 16px;
  }
}
@media (max-width: 568px) {
  body {
    --container: 12px;
    --containerFull: 12px;
    --sectionMargin: 60px;
  }
}

.collapse:not(.collapse_show) {
  display: none;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #f8f4e8;
  font-family: "TT Drugs";
}

.main {
  flex: 1;
  padding-top: var(--header);
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 var(--container);
}

.circle {
  --size: 32px;
  flex: 0 0 var(--size);
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

label {
  cursor: pointer;
}

.h1 {
  /* 1440/H1 */
  font-family: "TT Drugs";
  font-size: 75px;
  font-weight: 400;
  line-height: 130%; /* 97.5px */
}
@media (max-width: 1024px) {
