.slider-btn {
  --size: 54px;
  background: #f3f3f3;
  transition: 0.4s;
}
.slider-btn.swiper-button-disabled {
  opacity: 0;
}
.slider-btn.transparent {
  background: transparent;
  border: 1px solid #949698;
}
.slider-btn.transparent svg path {
  stroke: white;
}
.slider-btn svg {
  width: 16px;
  height: 16px;
}
.slider-btn::after {
  display: none;
}

@media (max-width: 1024px) {
  .pc {
    display: none;
  }
}

.tablet {
  display: none;
}
@media (max-width: 1024px) {
  .tablet {
    display: block;
  }
}
@media (max-width: 568px) {
  .tablet {
    display: none;
  }
}

.mobile {
  display: none;
}
@media (max-width: 1024px) {
  .mobile {
    display: none;
  }
}
@media (max-width: 568px) {
  .mobile {
    display: block;
  }
}

.header {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  transition: 0.4s;
}
.header.light {
  background-color: #f8f4e8;
}
.header.light .header__burger span, .header.light .header__burger::before, .header.light .header__burger::after {
  background-color: #202929;
}
.header.light .header__logo path {
  fill: #0f6062;
}
.header.light .header__links a {
  color: #202929;
}
.header.light .header__icons svg path {
  stroke: #202929;
}
.header.dark {
  background-color: #202929;
}
.header.dark .header__burger span, .header.dark .header__burger::before, .header.dark .header__burger::after {
  background-color: #f8f4e8;
}
.header.dark .header__logo path {
  fill: #f8f4e8;
}
.header.dark .header__links a {
  color: #f8f4e8;
}
.header.dark .header__icons svg path {
  stroke: #f8f4e8;
}
.header__wrapper {
  height: var(--header);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__left {
  display: flex;
  align-items: center;
  gap: 36px;
}
@media (max-width: 1024px) {
  .header__left {
    gap: 0;
  }
}
@media (max-width: 568px) {
  .header__left {
    gap: 16px;
  }
}
.header__burger {
  width: 24px;
  height: 24px;
  cursor: pointer;
  position: relative;
}
.header__burger span, .header__burger::before, .header__burger::after {
  content: "";
  position: absolute;
  height: 1px;
  width: 18px;
  background-color: #f8f4e8;
  transition: 0.4s;
}
.header__burger span {
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}
.header__burger::before {
  top: 5px;
  left: 50%;
  translate: -50% 0;
}
.header__burger::after {
  bottom: 5px;
  left: 50%;
  translate: -50% 0;
}
.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}
.header__logo path {
  transition: 0.4s;
}
@media (max-width: 1024px) {
  .header__logo {
    position: relative;
    top: unset;
    left: unset;
    translate: unset;
    margin-left: 30px;
    margin-right: 64px;
  }
}
@media (max-width: 750px) {
  .header__logo {
    margin-left: 12px;
    margin-right: 24px;
  }
}
@media (max-width: 568px) {
  .header__logo {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
  }
}
.header__links {
  display: flex;
  align-items: center;
  gap: 32px;
}
.header__links a {
  color: #f8f4e8;
  transition: 0.4s;
  leading-trim: both;
  text-edge: cap;
  /* 1440/Body 2 medium */
  font-family: "TT Drugs";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%; /* 22.4px */
}
@media (max-width: 1024px) {
  .header__links a {
    leading-trim: both;
    text-edge: cap;
    /* 768/Body 2 medium */
    font-family: "TT Drugs";
    font-size: 14px;
    font-weight: 500;
    line-height: 140%; /* 19.6px */
  }
}
@media (max-width: 750px) {
  .header__links {
    gap: 24px;
  }
}
@media (max-width: 568px) {
  .header__links {
    display: none;
  }
}
.header__icons {
  display: flex;
  align-items: center;
  gap: 24px;
}
.header__icons a {
  height: 20px;
  width: 20px;
}
.header__icons a svg path {
  transition: 0.4s;
}
@media (max-width: 1024px) {
  .header__icons {
    gap: 20px;
  }
}
@media (max-width: 750px) {
  .header__icons {
    gap: 10px;
  }
}
@media (max-width: 568px) {
  .header__icons {
    gap: 16px;
  }
  .header__icons li:first-child {
    display: none;
  }
}

.header-search {
  display: none;
}
@media (max-width: 568px) {
  .header-search {
    display: flex;
  }
}

.footer {
  background-color: #202929;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  gap: 100px;
  padding-top: 80px;
  padding-bottom: 40px;
}
@media (max-width: 1024px) {
  .footer__wrapper {
    padding-top: 40px;
    gap: 32px;
  }
}
.footer__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 1024px) {
  .footer__list {
    flex-direction: column;
    flex-wrap: unset;
  }
}
.footer__mail {
  flex: 0 0 308px;
}
@media (max-width: 1024px) {
  .footer__mail {
    flex: unset;
    margin-bottom: 32px;
  }
}
.footer__contacts {
  flex: 0 0 205px;
}
@media (max-width: 1024px) {
  .footer__contacts {
    flex: unset;
    margin-top: 24px;
  }
}
.footer__contacts > p {
  /* 1440/Body small */
  font-family: "TT Drugs";
  font-size: 12px;
  color: #949698;
  display: flex;
  gap: 4px;
}
.footer__contacts > p::before {
  content: "*";
  font-size: 11px;
  color: #f8f4e8;
}
.footer__title {
  margin-bottom: 28px;
  /* 1440/H5 medium */
  font-family: "TT Drugs";
  font-size: 18px;
  font-weight: 500;
  color: rgba(248, 244, 232, 0.8);
}
@media (max-width: 1024px) {
  .footer__title {
    margin-bottom: 20px;
    /* 390/H5 medium */
